import Vue from 'vue'
import Bus from '../Bus'
import BScroll from 'better-scroll'
export default {
  props: ['goods'],
  data () {
    return {
      sider: 0,//侧边栏自定义变量
      count: 0,//点餐数量
      grossAmount:0 //总金额
    }
  },
  created () {
    this.classMap = ['decrease', 'discount', 'guarantee', 'invoice', 'special']
  },
  methods: {
    countfn (foodsLIst) {
      if (foodsLIst.count) {
          foodsLIst.count++
          this.grossAmount+=foodsLIst.price*(foodsLIst.count-(foodsLIst.count-1))
      } else {
        Vue.set(foodsLIst, 'count', 1)
        this.grossAmount+=foodsLIst.price
      }
      this.$store.state.grossAmount=this.grossAmount
      this.$store.commit('getAmount')
      // Bus.$emit('getTarget', this.grossAmount);
    },
    minusFn (foodsLIst) {
      this.grossAmount-=foodsLIst.price*(foodsLIst.count-(foodsLIst.count-1))
      foodsLIst.count--
      this.$store.state.grossAmount=this.grossAmount
       this.$store.commit('getAmount')
      // Bus.$emit('getTarget', this.grossAmount);
    }
  },
  computed: {

  }
}



